<template>
  <div id="translateOutput" class="text-center">
      <h2>{{translatedText}}</h2>
  </div>
</template>

<script>
    export default {
        name: 'translateOutput',
        props: ['translatedText']
    }
</script>

<style>
    #translateOutput {
        color: aquamarine;
        font-size: 80px;
        margin-top: 0.5em;
    }
</style>


